
import { Options, Vue } from "vue-class-component";
import { OfficeFlow, FlowValidate, Popup, Toast } from "../packages";
import chooseApproval from "./components/choose-approval.vue";
import chooseCondition from "./components/choose-condition.vue";
import ClipboardJS from "clipboard";
new ClipboardJS(".copybtn");

@Options({
  components: {
    OfficeFlow: OfficeFlow,
    Popup,
    chooseApproval,
    chooseCondition,
  },
})
export default class ScorePage extends Vue {
  // 
  cnode = {
    name: "",
    type: "start",
    nodeName: "",
    properties: {
      // properties
    },
  };

  // 
  adddata = [
    { key: "approver", value: "", icon: "Stamp", color: "#3d91eb" },
    {
      key: "notifier",
      value: "",
      icon: "Promotion",
      color: "orange",
    },
    { key: "hehe", value: "", icon: "UserFilled", color: "blue" },
    { key: "lala", value: "", icon: "StarFilled", color: "green" },
    { key: "condition", value: "", icon: "Branch", color: "#333" },
  ];

  // 
  popupshow = false;
  nodeData: any = {};
  conditionsData: any[] = [];

  // 
  onSubmit() {
    FlowValidate(this.cnode)
      .then((ret) => {
        console.log("validate success!");
        console.log(this.cnode);
      })
      .catch((err) => {
        console.log(err);
        Toast({
          allowHtml: true,
          message: `${!err.ValidateNodeNames ? ",<br/>" : ""} ${
            !err.flows ? "!" : ""
          }`,
        });
      });
  }

  // ,
  onChoose({ type, node, conditions }: any) {
    this.popupshow = true;
    this.nodeData = node;
    this.conditionsData = conditions;
  }

  // 
  onAdd({ type, node }: any) {
    //
  }
}
